import React, { useEffect, useState } from 'react';
import {
    useHistory,
    useLocation
} from 'react-router-dom';
import { connect } from 'react-redux';
import MaterialTable from 'material-table';

import {
    Button,
    TextField,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle
} from '@material-ui/core';

import { lighten, makeStyles } from '@material-ui/core/styles';

import { authHeader } from '../../_helpers';
import { common } from '../../_constants/common';

import '../../App.css';

const apiUrl = 'http://localhost:5000';
const fetchEmployeesAPI = `${apiUrl}/admin/employees`;
const generateOTPAPI = `${apiUrl}/admin/employee/generateotp`;
const verifyOTPAPI = `${apiUrl}/admin/employee/verifyotp`;
const resendInviteAPI = `${apiUrl}/admin/employee/resendinvite`;
const forgotPasswordAPI = `${apiUrl}/admin/employee/forgotpassword`;
const resetPasswordAPI = `${apiUrl}/admin/employee/resetpassword`;

const useStyles = makeStyles((theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	visuallyHidden: {
		border: 0,
		clip: 'rect(0 0 0 0)',
		height: 1,
		margin: -1,
		overflow: 'hidden',
		padding: 0,
		position: 'absolute',
		top: 20,
		width: 1,
	},
}));

function TableActionButton(props) {
    console.log("TableActionButton", props);
    return (
        <span>{props.title}</span>
    );
}

function EmployeeList(props) {
    console.log("EmployeeList called");
    const classes = useStyles();

    let userData = localStorage.getItem('user');
    userData = JSON.parse(userData);
    console.log("userData", userData);

    let history = useHistory();
    console.log("history", history);

    let location = useLocation();
    console.log("location", location);

    let { from } = location.state || { from: { pathname: "/adminpanel/" } };
    if(!userData.acl.TRAILER.includes("VIEW")) {
        history.replace(from);
    }

    const [rows, setRows] = useState([]);
    const [totalCount, setTotalCount] = useState(0);

    //-------------------------------------------------------------------------
    
    const [dialogState, setDialogState] = useState(false);
    const [otp, setOTP] = useState("");
    const [otpVerifySucess, setOTPVerifySucess] = useState("");
    const [mobile, setMobile] = useState("");

    function handleClickOpen() {
        setDialogState(true);
    }
    
    function handleClose() {
        setDialogState(false);
    }

    function handleVerifyOTP() {
        const requestOptions = {
            method: 'POST',
            headers: { 
                ...authHeader(),
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ 
                mobile: mobile, 
                // mobile: "919664815262",
                country: common.country,
                otp: otp 
            })
        };
        console.log("requestOptions", requestOptions);
    
        fetch(verifyOTPAPI, requestOptions)
        .then( res => res.json() )
        .then( res => {
            console.log(`${verifyOTPAPI} Response`, res);
            // rowData.driverLicense.verified = true;
            // rowData.driverLicense.accepted = true;
            if(tableRef.current) {
                tableRef.current.onQueryChange();
            }

            setOTPVerifySucess("OTP Verified Successfully")
            setMobile("");
            setTimeout(() => {
                handleClose();
            }, 5000);
            
        })
        .catch( error => {
            console.log(`${verifyOTPAPI} Error`, error);
        });
    }

    //-------------------------------------------------------------------------
    
    const tableRef = React.createRef();

    return (
        <div className={`t2y-list t2y-employeelist ${classes.root}`}>
            <Dialog open={dialogState} 
                    onClose={handleClose} 
                    aria-labelledby="form-dialog-title"
                    disableBackdropClick={true}
                    disableEscapeKeyDown={true}>
                <DialogTitle id="form-dialog-title">OTP Verification</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Please Enter OTP
                    </DialogContentText>
                    { 
                        (otpVerifySucess !== "") &&
                        <DialogContentText>{otpVerifySucess}</DialogContentText>
                    }
                    <TextField
                        autoFocus
                        fullWidth
                        margin="dense"
                        id="name"
                        label="OTP"
                        type="otp"
                        value={otp}
                        onChange={ (e) => { 
                            setOTP( e.target.value );
                        } }
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={handleVerifyOTP} color="primary">
                        Verify
                    </Button>
                </DialogActions>
            </Dialog>

            <MaterialTable 
                title="Employees"
                tableRef={tableRef}
                columns={[
                    { 
                        title: 'ID', 
                        field: 'adminEmployeeRef', 
                        render: rowData => {
                            return rowData.adminEmployeeRef
                        } 
                    },
                    { 
                        title: 'Name', 
                        field: 'name', 
                        render: rowData => {
                            return (rowData.name && rowData.name) ? `${rowData.name}` : rowData.name
                        } 
                    },
                    { 
                        title: 'Email', 
                        field: 'email', 
                        render: rowData => {
                            return rowData.email
                        } 
                    },
                    { 
                        title: 'Email Verified', 
                        field: 'isEmailVerified', 
                        render: rowData => {
                            return rowData.isEmailVerified ? "Yes": "No"
                        } 
                    },
                    { 
                        title: 'Mobile', 
                        field: 'mobile', 
                        render: rowData => {
                            return rowData.mobile
                        } 
                    },
                    { 
                        title: 'Mobile Verified', 
                        field: 'isMobileVerified', 
                        render: rowData => {
                            return rowData.isMobileVerified ? "Yes": "No"
                        } 
                    },
                    { 
                        title: 'Is Owner', 
                        field: 'isOwner', 
                        render: rowData => {
                            return rowData.isOwner ? "Yes": "No"
                        } 
                    }
                ]}
                data={ 
                    query => {
                        console.log("Employee MaterialTable data", query);
                        const rowsPerPage = query.pageSize;
                        const page = query.page;

                        const requestOptions = {
                            method: 'GET',
                            headers: authHeader()
                        };
                        // return fetch(`${fetchEmployeesAPI}?count=${rowsPerPage}&skip${(page * rowsPerPage)}`, requestOptions)
                        return new Promise((resolve, reject) => {
                            // fetch(`${fetchEmployeesAPI}`, requestOptions)
                            fetch(`${fetchEmployeesAPI}?count=${rowsPerPage}&skip=${(page * rowsPerPage)}`, requestOptions)
                            .then( res => res.json() )
                            .then( res => {
                                console.log(`${fetchEmployeesAPI} Response`, res);
                                const employeeList = res.employeeList || [];
                                const totalCount = res.totalCount ? res.totalCount : 0;
                                
                                let newRows = [];
                                setRows((currentRows) => {
                                    console.log((typeof currentRows), (typeof employeeList));
                                    newRows = [
                                        ...currentRows,
                                        ...employeeList
                                    ];
                                    console.log("newRows", newRows);
                                    return newRows;
                                });

                                setTotalCount(totalCount);

                                resolve({
                                    data: employeeList,
                                    page: page,
                                    totalCount: totalCount
                                })
                            })
                            .catch( error => {
                                console.log("getEmployees Error", error);
                                reject(error);
                            });
                        });
                    }
                }
                actions={
                    [
                        {
                            icon: "create",
                            tooltip: "Edit Employee",
                            onClick: (event, rowData) => {
                                console.log("Edit onClick", event, rowData);
                                history.push(`/adminpanel/employee/edit/${rowData._id}`);
                            }
                        },
                        {
                            icon: () => { return (<TableActionButton title="Resend OTP" />) },
                            tooltip: "Resend OTP",
                            onClick: (event, rowData) => {
                                console.log("Resend OTP onClick", event, rowData);
                                const requestOptions = {
                                    method: 'POST',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ 
                                        mobile: rowData.mobile, 
                                        // mobile: "919664815262",
                                        country: common.country 
                                    })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(generateOTPAPI, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${generateOTPAPI} Response`, res);
                                    // rowData.driverLicense.verified = true;
                                    // rowData.driverLicense.accepted = true;
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }

                                    setMobile(rowData.mobile);
                                    handleClickOpen();
                                })
                                .catch( error => {
                                    console.log(`${generateOTPAPI} Error`, error);
                                });
                            }
                        },
                        {
                            icon: () => { return (<TableActionButton title="Verify Email" />) } ,
                            tooltip: "Verify Email",
                            onClick: (event, rowData) => {
                                console.log("Verify Email", event, rowData);
                                const requestOptions = {
                                    method: 'POST',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ 
                                        email: rowData.email
                                    })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(resendInviteAPI, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${resendInviteAPI} Response`, res);
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }
                                })
                                .catch( error => {
                                    console.log(`${resendInviteAPI} Error`, error);
                                });
                            }
                        },
                        {
                            icon: () => { return (<TableActionButton title="Forgot Password" />) } ,
                            tooltip: "Forgot Password",
                            onClick: (event, rowData) => {
                                console.log("Forgot Password", event, rowData);
                                const requestOptions = {
                                    method: 'PUT',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ 
                                        email: rowData.email
                                    })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(forgotPasswordAPI, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${forgotPasswordAPI} Response`, res);
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }
                                })
                                .catch( error => {
                                    console.log(`${forgotPasswordAPI} Error`, error);
                                });
                            }
                        },
                        {
                            icon: () => { return (<TableActionButton title="Resend Invite" />) } ,
                            tooltip: "Resend Invite",
                            onClick: (event, rowData) => {
                                if(!rowData.acceptedInvite) {
                                    console.log("Resend Invite", event, rowData);
                                    const requestOptions = {
                                        method: 'POST',
                                        headers: { 
                                            ...authHeader(),
                                            'Content-Type': 'application/json'
                                        },
                                        body: JSON.stringify({ 
                                            email: rowData.email
                                        })
                                    };
                                    console.log("requestOptions", requestOptions);
                                
                                    fetch(resendInviteAPI, requestOptions)
                                    .then( res => res.json() )
                                    .then( res => {
                                        console.log(`${resendInviteAPI} Response`, res);
                                        if(tableRef.current) {
                                            tableRef.current.onQueryChange();
                                        }
                                    })
                                    .catch( error => {
                                        console.log(`${resendInviteAPI} Error`, error);
                                    });
                                } else {
                                    console.log("Resend Invite", rowData.acceptedInvite);
                                }
                            }
                        }
                    ]
                }
                options={
                    {
                        actionsColumnIndex: -1
                    }
                }
            />
        </div>
    )
}

// cancel, check-circle


export default EmployeeList;