import React, { useState, useEffect, Fragment } from 'react';
import {
    useLocation,
    useHistory,
    useParams
} from "react-router-dom";
import moment from 'moment';

import {
    Avatar,
    Button,
    CssBaseline,
    TextField,
    FormGroup,
    FormControl,
    FormHelperText,
    FormControlLabel,
    Checkbox,
    Link,
    Grid,
    Box,
    LockOutlinedIcon,
    Typography,
    Container,
    TextareaAutosize,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    InputLabel,
    Select,
    MenuItem
} from '@material-ui/core';


import { lighten, makeStyles } from '@material-ui/core/styles';

import { authHeader } from '../../_helpers';

import { common } from '../../_constants/common';

import '../../App.css';

const useStyles = makeStyles((theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 200,
	},
	visuallyHidden: {
		border: 0,
		clip: 'rect(0 0 0 0)',
		height: 1,
		margin: -1,
		overflow: 'hidden',
		padding: 0,
		position: 'absolute',
		top: 20,
		width: 1,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    }
}));

const apiUrl = 'http://localhost:5000';
const getCustomer = `${apiUrl}/admin/customer`;
const addCustomer = `${apiUrl}/admin/customer`; // POST
const updateCustomer = `${apiUrl}/admin/customer`; // PUT

export default function CustomerEdit(props) {
    console.log("CustomerEdit", props);

    const classes = useStyles();

    let userData = localStorage.getItem('user');
    userData = JSON.parse(userData);
    console.log("userData", userData);

    let history = useHistory();
    console.log("history", history);

    let location = useLocation();
    console.log("location", location);

    const { customerId } = useParams();
    console.log("customerId", customerId);

    let { from } = location.state || { from: { pathname: "/adminpanel/" } };
    if(!customerId && !userData.acl.CUSTOMERS.includes("ADD")) {
        history.replace(from);
    }
    if(customerId && !userData.acl.CUSTOMERS.includes("UPDATE")) {
        history.replace(from);
    }

    const customerStart = {
        name: "",
        address: {
            text: "",
            pincode: "",
            coordinates: "",
            country: common.country
        },
        dob: moment().subtract(18, "years").format("YYYY-MM-DD"),
        driverLicense: {
            card: "",
            expiry: moment().add(1, "year").format("YYYY-MM"),
            state: common.states[0],
            scanLink: ""
        },
        email: "",
        isEmailVerified: false,
        mobile: "",
        isMobileVerified: false,
        photoLink: "",

        photo: null,
        driverLicenseScan: null
    };

    const [loadOnce, setLoadOnce] = useState(0);
    const [customer, setCustomer] = useState(customerStart);

    const [successMessage, setSuccessMessage] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    useEffect(() => {
        if(customerId) {
            const requestOptions = {
                method: 'GET',
                headers: authHeader(),

            };
            fetch(`${getCustomer}?id=${customerId}`, requestOptions)
            .then( res => res.json() )
            .then( res => {
                console.log("Customer API Call Response", res.customerObj);
                const customerObj = res.customerObj;
                
                setCustomer({
                    name: customerObj.name,
                    address: {
                        text: customerObj.address.text,
                        pincode: customerObj.address.pincode,
                        coordinates: customerObj.address.coordinates.join(",")
                    },
                    dob: customerObj.dob,
                    driverLicense: {
                        card: customerObj.driverLicense.card,
                        expiry: customerObj.driverLicense.expiry,
                        state: customerObj.driverLicense.state,
                        scanLink: customerObj.driverLicense.scan
                    },
                    email: customerObj.email,
                    isEmailVerified: customerObj.isEmailVerified,
                    mobile: customerObj.mobile,
                    isMobileVerified: customerObj.isMobileVerified,
                    photoLink: customerObj.photo,

                    photo: null,
                    driverLicenseScan: null
                });
            })
            .catch( err => {
                console.log("Error occurred while fetching Customer data", err);
            })
        }
    }, [loadOnce]);

    const [formValidErrorMessage, setFormValidErrorMessage] = useState({
        name: "",
        address: {
            text: "",
            pincode: "",
            coordinates: ""
        },
        dob: "",
        driverLicense: {
            card: "",
            expiry: "",
            state: "",
            scan: ""
        },
        email: "",
        mobile: "",
        photo: ""
    });

    async function handleSubmit(e) {
        e.preventDefault();

        const isError = (
            (formValidErrorMessage.name !== "") ||
            (formValidErrorMessage.address.text !== "") ||
            (formValidErrorMessage.address.pincode !== "") ||
            (formValidErrorMessage.address.coordinates !== "") ||
            (formValidErrorMessage.dob !== "") ||
            (formValidErrorMessage.driverLicense.card !== "") ||
            (formValidErrorMessage.driverLicense.expiry !== "") ||
            (formValidErrorMessage.driverLicense.state !== "") ||
            (formValidErrorMessage.driverLicense.scan !== "") ||
            (formValidErrorMessage.email !== "") ||
            (formValidErrorMessage.mobile !== "") ||
            (formValidErrorMessage.photo !== "")
        );

        if(isError) {
            return false;
        }

        let requestOptions = {
            method: customerId ? "PUT" : "POST"
        };

        console.log("customer.driverLicense.expiry", customer.driverLicense.expiry);
        
        customer.dob = moment(customer.dob).format("YYYY-MM-DD");
        customer.driverLicense.expiry = moment(customer.driverLicense.expiry, "YYYY-MM").format("MM/YY");

        console.log("customer.driverLicense.expiry", customer.driverLicense.expiry);

        if(customer.photo || customer.driverLicenseScan) {
            const formData = new FormData();

            formData.append('name', customer.name);
            formData.append('address', JSON.stringify(customer.address));
            formData.append('dob', customer.dob);
            formData.append('driverLicense', JSON.stringify(customer.driverLicense));
            formData.append('email', customer.email);
            formData.append('mobile', customer.mobile);
        
            if(customer.photo) {
                formData.append('photo', customer.photo, customer.photo.name);
            }
            if(customer.driverLicenseScan) {
                formData.append('driverLicenseScan', customer.driverLicenseScan, customer.driverLicenseScan.name);
            }

            // append files

            if(customerId) {
                formData.append('_id', customerId);
                formData.append('customerId', customerId);
            }

            requestOptions.headers = { 
                ...authHeader()
            };
            requestOptions.body = formData;
        } else {
            const reqObj = {
                name: customer.name,
                address: customer.address,
                dob: customer.dob,
                driverLicense: customer.driverLicense,
                email: customer.email,
                mobile: customer.mobile
            };

            if(customerId) {
                reqObj._id = customerId;
                reqObj.customerId = customerId;
            }

            requestOptions.headers = { 
                ...authHeader(),
                'Content-Type': 'application/json'
            };
            requestOptions.body = JSON.stringify(reqObj);
        }

        console.log("requestOptions", requestOptions);

        const apiUrl = customerId ? updateCustomer : addCustomer;
    
        fetch(apiUrl, requestOptions)
        .then( res => res.json() )
        .then( res => {
            console.log(`${apiUrl} Response`, res);

            const customerObj = res.customerObj;

            setCustomer({
                name: customerObj.name,
                address: {
                    text: customerObj.address.text,
                    pincode: customerObj.address.pincode,
                    coordinates: customerObj.address.coordinates.join(",")
                },
                dob: customerObj.dob,
                driverLicense: {
                    card: customerObj.driverLicense.card,
                    expiry: customerObj.driverLicense.expiry,
                    state: customerObj.driverLicense.state,
                    scanLink: customerObj.driverLicense.scan
                },
                email: customerObj.email,
                isEmailVerified: customerObj.isEmailVerified,
                mobile: customerObj.mobile,
                isMobileVerified: customerObj.isMobileVerified,
                photoLink: customerObj.photo,

                photo: null,
                driverLicenseScan: null
            });
            
            setErrorMessage("");
            setSuccessMessage("Successfully saved Customer data");

            setTimeout(() => {
                setSuccessMessage("");
            }, 5000);
        })
        .catch( error => {
            console.log(`${apiUrl} Error`, error);

            setErrorMessage("Error occurred while saving Customer data");
        });
    }

    
    return (
        <Container component="main" maxWidth="sm">

            <CssBaseline />

            <div className={classes.paper}>

                <Typography component="h1" variant="h5">
                    { customerId ? "Edit Customer" : "Add Customer" }
                </Typography>

                { 
                    (successMessage !== "") &&
                    <Typography component="h6" variant="h5">
                        {successMessage}
                    </Typography>
                }

                { 
                    (errorMessage !== "") &&
                    <Typography component="h6" variant="h5">
                        {errorMessage}
                    </Typography>
                }
            
                <form className={classes.form} noValidate>

                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="name"
                        label="Name"
                        name="name"
                        autoComplete="name"
                        autoFocus
                        error={(formValidErrorMessage.name !== "")}
                        helperText={formValidErrorMessage.name}
                        value={customer.name}
                        onChange={ (e) => { 
                            setCustomer({
                                ...customer,
                                name: e.target.value
                            });
                        } }
                    />

                    <Box className="SectionContainer">

                        <Typography variant="subtitle1">
                            Address
                        </Typography>

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="address_text"
                            label="Address Line"
                            name="address_text"
                            autoComplete="address_text"
                            autoFocus
                            error={(formValidErrorMessage.address.text !== "")}
                            helperText={formValidErrorMessage.address.text}
                            value={customer.address.text}
                            onChange={ (e) => { 
                                setCustomer({
                                    ...customer,
                                    address: {
                                        ...customer.address,
                                        text: e.target.value
                                    }
                                });
                            } }
                        />

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="address_pincode"
                            label="Pincode"
                            name="address_pincode"
                            autoComplete="address_pincode"
                            autoFocus
                            error={(formValidErrorMessage.address.pincode !== "")}
                            helperText={formValidErrorMessage.address.pincode}
                            value={customer.address.pincode}
                            onChange={ (e) => { 
                                setCustomer({
                                    ...customer,
                                    address: {
                                        ...customer.address,
                                        pincode: e.target.value
                                    }
                                });
                            } }
                        />

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="address_coordinates"
                            label="Coordinates (Latitude,Longitude format)"
                            name="address_coordinates"
                            autoComplete="address_coordinates"
                            autoFocus
                            error={(formValidErrorMessage.address.coordinates !== "")}
                            helperText={formValidErrorMessage.address.coordinates}
                            value={customer.address.coordinates}
                            onChange={ (e) => { 
                                setCustomer({
                                    ...customer,
                                    address: {
                                        ...customer.address,
                                        coordinates: e.target.value
                                    }
                                });
                            } }
                        />

                    </Box>

                    <TextField 
                        variant="outlined"
                        margin="normal"
                        aria-label="dob" 
                        required
                        fullWidth
                        type="date"
                        id="dob"
                        label="dob"
                        name="dob"
                        autoComplete="dob"
                        autoFocus
                        error={(formValidErrorMessage.dob !== "")}
                        helperText={formValidErrorMessage.dob}
                        value={customer.dob}
                        onChange={ (e) => { 
                            setCustomer({
                                ...customer,
                                dob: e.target.value
                            });
                        } } />
                    

                    <Box className="SectionContainer">

                        <Typography variant="subtitle1">
                            Driver License
                        </Typography>

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="driverLicense_card"
                            label="Driver License Number"
                            name="driverLicense_card"
                            autoComplete="driverLicense_card"
                            autoFocus
                            error={(formValidErrorMessage.driverLicense.card !== "")}
                            helperText={formValidErrorMessage.driverLicense.card}
                            value={customer.driverLicense.card}
                            onChange={ (e) => { 
                                setCustomer({
                                    ...customer,
                                    driverLicense: {
                                        ...customer.driverLicense,
                                        card: e.target.value
                                    }
                                });
                            } }
                        />

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            type="month"
                            id="driverLicense_expiry"
                            label="Expiry Date"
                            name="driverLicense_expiry"
                            autoComplete="driverLicense_expiry"
                            autoFocus
                            error={(formValidErrorMessage.driverLicense.expiry !== "")}
                            helperText={formValidErrorMessage.driverLicense.expiry}
                            value={customer.driverLicense.expiry}
                            onChange={ (e) => { 
                                setCustomer({
                                    ...customer,
                                    driverLicense: {
                                        ...customer.driverLicense,
                                        expiry: e.target.value
                                    }
                                });
                            } }
                        />

                        {
                            common.states && 
                            <FormControl required>
                                <InputLabel id="state-label">State</InputLabel>
                                <Select
                                    labelId="state-label"
                                    id="stateSelect"
                                    value={customer.driverLicense.state}
                                    onChange={ (e) => { 
                                        setCustomer({
                                            ...customer,
                                            driverLicense: {
                                                ...customer.driverLicense,
                                                state: e.target.value
                                            }
                                        });
                                    } }
                                    className={classes.selectEmpty}
                                >
                                    {
                                        common.states.map((state, stateIndex) => {
                                            console.log("state", state, stateIndex);
                                            return (
                                                <MenuItem key={`state-${stateIndex}`} value={state}>{state}</MenuItem>
                                            )
                                        })
                                    }
                                </Select>
                                <FormHelperText>Required</FormHelperText>
                            </FormControl>
                        }

                        <Box className="SectionContainer">

                            <Typography variant="subtitle1">
                                Scan ( Scan of Front and Back side of Driver License in a single File )
                            </Typography>

                            {
                                customer.driverLicense.scanLink && !customer.driverLicenseScan && 
                                (
                                    <Box className="SectionContainer">
                                        <a href={customer.driverLicense.scanLink}>Scan</a>
                                    </Box>
                                )
                            }

                            <input
                                accept="image/jpeg, image/jpg, image/png"
                                id="raised-button-file"
                                multiple
                                type="file"
                                name="scan"
                                onChange={ (e) => {
                                    console.log("scan", e.target.files);
                                    setCustomer({
                                        ...customer,
                                        driverLicenseScan: e.target.files[0]
                                    });
                                }}
                                />

                            {/* <label htmlFor="raised-button-file">
                                <Button variant="raised" component="span" className={classes.button}>
                                    Upload
                                </Button>
                            </label>  */}
                        </Box>

                    </Box>

                    <FormGroup className="SectionContainer">

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="Email"
                            name="email"
                            autoComplete="email"
                            autoFocus
                            error={(formValidErrorMessage.email !== "")}
                            helperText={formValidErrorMessage.email}
                            value={customer.email}
                            onChange={ (e) => { 
                                setCustomer({
                                    ...customer,
                                    email: e.target.value
                                });
                            } }
                        />

                        <FormControlLabel
                            control={
                                <Checkbox 
                                    disabled={true}
                                    checked={customer.isEmailVerified} 
                                    name="isEmailVerified"
                                    onChange={(e) => { 
                                        console.log("onChange isEmailVerified", e);
                                        setCustomer({
                                            ...customer,
                                            isEmailVerified: !customer.isEmailVerified
                                        });
                                    }}  />
                            }
                            label="Is Email Verified?"
                        />

                    </FormGroup>

                    <FormGroup className="SectionContainer">

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="mobile"
                            label="Mobile"
                            name="mobile"
                            autoComplete="mobile"
                            autoFocus
                            error={(formValidErrorMessage.mobile !== "")}
                            helperText={formValidErrorMessage.mobile}
                            value={customer.mobile}
                            onChange={ (e) => { 
                                setCustomer({
                                    ...customer,
                                    mobile: e.target.value
                                });
                            } }
                        />

                        <FormControlLabel
                            control={
                                <Checkbox 
                                    disabled={true}
                                    checked={customer.isMobileVerified} 
                                    name="isMobileVerified"
                                    onChange={(e) => { 
                                        console.log("onChange isMobileVerified", e);
                                        setCustomer({
                                            ...customer,
                                            isMobileVerified: !customer.isMobileVerified
                                        });
                                    }}  />
                            }
                            label="Is Mobile Verified?"
                        />

                    </FormGroup>

                    <Box className="SectionContainer">

                        <Typography variant="subtitle1">
                            Photo
                        </Typography>

                        {
                            customer.photoLink && !customer.photo && 
                            (
                                <Box className="SectionContainer">
                                    <a href={customer.photoLink}>Photo</a>
                                </Box>
                            )
                        }

                        <input
                            accept="image/jpeg, image/jpg, image/png"
                            id="raised-button-file"
                            multiple
                            type="file"
                            name="photo"
                            onChange={ (e) => {
                                console.log("photo", e.target.files);
                                setCustomer({
                                    ...customer,
                                    photo: e.target.files[0]
                                });
                            }}
                            />

                        {/* <label htmlFor="raised-button-file">
                            <Button variant="raised" component="span" className={classes.button}>
                                Upload
                            </Button>
                        </label>  */}
                    </Box>



                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        onClick={handleSubmit}
                    >
                        { customerId ?  "Update" : "Save"}
                    </Button>

                </form>

            </div>

        </Container>
    )
}