import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import MaterialTable from 'material-table';

import {
    ButtonGroup,
    Button
} from '@material-ui/core';

import { lighten, makeStyles } from '@material-ui/core/styles';

import { authHeader } from '../../_helpers';

import '../../App.css';

const apiUrl = 'http://localhost:5000';
const fetchRentalsAPI = `${apiUrl}/admin/rentals`;
const saveRentalApproval = `${apiUrl}/admin/rental/approval`;

const approvalStatusEnum = {
    0: "Pending",
    1: "Approved",
    2: "Rejected"
}

const useStyles = makeStyles((theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
    },
    paperCenter: {
        width: '100%',
        textAlign: 'center',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	visuallyHidden: {
		border: 0,
		clip: 'rect(0 0 0 0)',
		height: 1,
		margin: -1,
		overflow: 'hidden',
		padding: 0,
		position: 'absolute',
		top: 20,
		width: 1,
    }
}));

function Rentals(props) {
    const classes = useStyles();

    const [rows, setRows] = React.useState([]);
    const [totalCount, setTotalCount] = React.useState(0);
    const [requestType, setRequestType] = React.useState("rental");
    // const [requestType, setRequestType] = React.useState("extension");
    // const [requestType, setRequestType] = React.useState("reschedule");
    
    const tableRef = React.createRef();

    return (
        <div className={classes.root}>

            <div className={classes.paperCenter}>

                <ButtonGroup size="large" color="primary" aria-label="large outlined primary button group">
                    <Button 
                        variant={ (requestType === "rental") ? "contained" : "outlined" } 
                        onClick={ () => {
                            console.log("Rental Button Clicked");
                            setRequestType("rental");
                            if(tableRef.current) {
                                tableRef.current.onQueryChange();
                            }
                        }
                    }>
                        Rental
                    </Button>
                    <Button 
                        variant={ (requestType === "extension") ? "contained" : "outlined" } 
                        onClick={ () => {
                            console.log("Extension Button Clicked");
                            setRequestType("extension");
                            if(tableRef.current) {
                                tableRef.current.onQueryChange();
                            }
                        }
                    }>
                        Extension
                    </Button>
                    <Button 
                        variant={ (requestType === "reschedule") ? "contained" : "outlined" } 
                        onClick={ () => {
                            console.log("Reschedule Button Clicked");
                            setRequestType("reschedule");
                            if(tableRef.current) {
                                tableRef.current.onQueryChange();
                            }
                        }
                    }>
                        Reschedule
                    </Button>
                </ButtonGroup>

            </div>
            
            <MaterialTable 
                title={ `${ requestType === "rental" ? "Rentals List" : ( requestType === "extension" ? "Extensions List" : "Reschedule List" ) }` }
                tableRef={tableRef}
                columns={[
                    { 
                        title: 'Rented Item Type', 
                        field: 'rentedItemType', 
                        render: rowData => {
                            return `${rowData.rentedItem}`
                        } 
                    },
                    { 
                        title: 'Rented Item', 
                        field: 'rentedItemName', 
                        render: rowData => {
                            return `${rowData.rentedItemName}`
                        } 
                    },
                    { 
                        title: 'Licensee Name', 
                        field: 'licenseeName', 
                        render: rowData => {
                            return `${rowData.licenseeName}`
                        } 
                    },
                    { 
                        title: 'Customer Name', 
                        field: 'customerName', 
                        render: rowData => {
                            return `${rowData.customerName}`
                        }
                    },
                    { 
                        title: 'Request Type', 
                        field: 'requestType', 
                        render: rowData => {
                            return `${rowData.requestType}`
                        } 
                    },
                    { 
                        title: 'Rental Period Start Date', 
                        field: 'rentalPeriodStart', 
                        render: rowData => {
                            return rowData.rentalPeriodStart ? rowData.rentalPeriodStart : "NA"
                        } 
                    },
                    { 
                        title: 'Rental Period End Date', 
                        field: 'rentalPeriodEnd', 
                        render: rowData => {
                            return rowData.rentalPeriodEnd ? rowData.rentalPeriodEnd : "NA"
                        } 
                    },
                    { 
                        title: 'Extend Till Date', 
                        field: 'extendTill', 
                        render: rowData => {
                            return rowData.extendTill ? rowData.extendTill : "NA"
                        } 
                    },
                    { 
                        title: 'Reschedule Start Date', 
                        field: 'scheduleStart', 
                        render: rowData => {
                            return rowData.scheduleStart ? rowData.scheduleStart : "NA"
                        } 
                    },
                    { 
                        title: 'Reschedule End Date', 
                        field: 'scheduleEnd', 
                        render: rowData => {
                            return rowData.scheduleEnd ? rowData.scheduleEnd : "NA"
                        } 
                    },
                    { 
                        title: 'Rental Request Status', 
                        field: 'isApproved',
                        render: rowData => {
                            return approvalStatusEnum[rowData.isApproved];
                        } 
                    },
                ]}
                data={ 
                    query => {
                        console.log("MaterialTable data", query);
                        const rowsPerPage = query.pageSize;
                        const page = query.page;

                        const requestOptions = {
                            method: 'GET',
                            headers: authHeader()
                        };
                        // return fetch(`${fetchRentalsAPI}?count=${rowsPerPage}&skip${(page * rowsPerPage)}`, requestOptions)
                        return new Promise((resolve, reject) => {
                            // fetch(`${fetchRentalsAPI}`, requestOptions)
                            fetch(`${fetchRentalsAPI}?type=${requestType}&count=${rowsPerPage}&skip=${(page * rowsPerPage)}`, requestOptions)
                            .then( res => res.json() )
                            .then( res => {
                                console.log(`${fetchRentalsAPI} Response`, res);
                                const requestList = res.requestList || [];
                                const totalCount = res.totalCount || 0;
                                
                                let newRows = [];
                                setRows((currentRows) => {
                                    console.log((typeof currentRows), (typeof requestList));
                                    newRows = [
                                        ...currentRows,
                                        ...requestList
                                    ];
                                    console.log("newRows", newRows);
                                    return newRows;
                                });

                                setTotalCount(totalCount);

                                resolve({
                                    data: requestList,
                                    page: page,
                                    totalCount: totalCount
                                })
                            })
                            .catch( error => {
                                console.log("getCustomer Error", error);
                                reject(error);
                            });
                        });
                    }
                }
                actions={
                    [
                        {
                            icon: "check_circle",
                            tooltip: "Approve",
                            // disabled: (rowData.isApproved !== 0),
                            onClick: (event, rowData) => {
                                console.log("Approve onClick", event, rowData);
                                // if((rowData.isApproved !== 0)) {

                                    let requestBody = { 
                                        rentalId: rowData.rentalId, 
                                        requestType: rowData.requestType,
                                        approvalStatus: "approved"
                                    };

                                    if(rowData.requestType === "extension") {
                                        requestBody.extensionId = rowData.extensionId;
                                    }
                                    if(rowData.requestType === "reschedule") {
                                        requestBody.scheduleId = rowData.scheduleId;
                                    }

                                    const requestOptions = {
                                        method: 'PUT',
                                        headers: { 
                                            ...authHeader(),
                                            'Content-Type': 'application/json'
                                        },
                                        body: JSON.stringify(requestBody)
                                    };
                                    console.log("requestOptions", requestOptions);
                                
                                    fetch(saveRentalApproval, requestOptions)
                                    .then( res => res.json() )
                                    .then( res => {
                                        console.log(`${saveRentalApproval} Response`, res);
                                        // rowData.driverLicense.verified = true;
                                        // rowData.driverLicense.accepted = true;
                                        if(tableRef.current) {
                                            tableRef.current.onQueryChange();
                                        }
                                    })
                                    .catch( error => {
                                        console.log(`${saveRentalApproval} Error`, error);
                                    });
                                // }
                            }
                        },
                        {
                            icon: "cancel",
                            tooltip: "Reject",
                            onClick: (event, rowData) => {
                                console.log("Reject onClick", event, rowData);
                                // if((rowData.isApproved !== 0)) {
                                    let requestBody = { 
                                        rentalId: rowData.rentalId, 
                                        requestType: rowData.requestType,
                                        approvalStatus: "rejected"
                                    };

                                    if(rowData.requestType === "extension") {
                                        requestBody.extensionId = rowData.extensionId;
                                    }
                                    if(rowData.requestType === "reschedule") {
                                        requestBody.scheduleId = rowData.scheduleId;
                                    }

                                    const requestOptions = {
                                        method: 'PUT',
                                        headers: { 
                                            ...authHeader(),
                                            'Content-Type': 'application/json'
                                        },
                                        body: JSON.stringify(requestBody)
                                    };
                                    console.log("requestOptions", requestOptions);
                                
                                    fetch(saveRentalApproval, requestOptions)
                                    .then( res => res.json() )
                                    .then( res => {
                                        console.log(`${saveRentalApproval} Response`, res);
                                        // rowData.driverLicense.verified = true;
                                        // rowData.driverLicense.accepted = false;
                                        if(tableRef.current) {
                                            tableRef.current.onQueryChange();
                                        }
                                    })
                                    .catch( error => {
                                        console.log(`${saveRentalApproval} Error`, error);
                                    });
                                // }
                            }
                        }
                    ]
                }
                options={
                    {
                        actionsColumnIndex: -1
                    }
                }
            />
        
        </div>
    )
}

// cancel, check-circle

export default Rentals;