import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import MaterialTable from 'material-table';
import {
    useHistory,
    useLocation,
    useParams
} from "react-router-dom";

import { lighten, makeStyles } from '@material-ui/core/styles';

import { authHeader } from '../../_helpers';

import '../../App.css';

const useStyles = makeStyles((theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	visuallyHidden: {
		border: 0,
		clip: 'rect(0 0 0 0)',
		height: 1,
		margin: -1,
		overflow: 'hidden',
		padding: 0,
		position: 'absolute',
		top: 20,
		width: 1,
	},
}));

const apiUrl = 'http://localhost:5000';
const fetchUpsellItemTypesAPI = `${apiUrl}/admin/upsellitems`;

export default function UpsellItemsList(props) {
    const classes = useStyles();

    let userData = localStorage.getItem('user');
    userData = JSON.parse(userData);
    console.log("userData", userData);

    let history = useHistory();
    console.log("history", history);

    let location = useLocation();
    console.log("location", location);

    let { from } = location.state || { from: { pathname: "/adminpanel/" } };
    if(!userData.acl.UPSELL.includes("VIEW")) {
        history.replace(from);
    }

    const [rows, setRows] = React.useState([]);
    const [totalCount, setTotalCount] = React.useState(0);
    
    const tableRef = React.createRef();

    return (
        <div className={classes.root}>
            <MaterialTable 
                title="Upsell Items List"
                tableRef={tableRef}
                search={false} 
                columns={[
                    { 
                        title: 'ID', 
                        field: 'upsellItemTypeRef', 
                        render: rowData => {
                            return rowData.upsellItemTypeRef
                        }
                    },
                    { 
                        title: 'Name', 
                        field: 'name', 
                        render: rowData => {
                            return rowData.name
                        } 
                    },
                    { 
                        title: 'Type', 
                        field: 'type', 
                        render: rowData => {
                            return rowData.type
                        } 
                    },
                    { 
                        title: 'Description', 
                        field: 'description', 
                        render: rowData => {
                            return rowData.description
                        } 
                    },
                    { 
                        title: 'Photos', 
                        field: 'photos', 
                        render: rowData => {
                            return (
                                <div>
                                    {
                                        rowData.photos.map((photo, photoIndex) => {
                                            return (
                                                <div>
                                                    <a key={rowData._id+"_photo"+photoIndex} href={photo}>Photo {(photoIndex + 1)}</a>
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                            );
                        } 
                    },
                    { 
                        title: 'Is Available?', 
                        field: 'availability', 
                        render: rowData => {
                            return rowData.availability ? "true" : "false"
                        } 
                    },
                    { 
                        title: 'is Featured?', 
                        field: 'isFeatured', 
                        render: rowData => {
                            return rowData.isFeatured ? "true" : "false"
                        } 
                    }
                ]} 
                data={ 
                    query => {
                        console.log("MaterialTable data", query);
                        const rowsPerPage = query.pageSize;
                        const page = query.page;

                        const requestOptions = {
                            method: 'GET',
                            headers: authHeader()
                        };
                        // return fetch(`${fetchUpsellItemTypesAPI}?count=${rowsPerPage}&skip${(page * rowsPerPage)}`, requestOptions)
                        return new Promise((resolve, reject) => {
                            // fetch(`${fetchUpsellItemTypesAPI}`, requestOptions)
                            fetch(`${fetchUpsellItemTypesAPI}?count=${rowsPerPage}&skip=${(page * rowsPerPage)}`, requestOptions)
                            .then( res => res.json() )
                            .then( res => {
                                console.log(`${fetchUpsellItemTypesAPI} Response`, res);
                                const upsellItemsList = res.upsellItemsList || [];
                                const totalCount = res.totalCount ? res.totalCount : 0;
                                
                                let newRows = [];
                                setRows((currentRows) => {
                                    console.log((typeof currentRows), (typeof upsellItemsList));
                                    newRows = [
                                        ...currentRows,
                                        ...upsellItemsList
                                    ];
                                    console.log("newRows", newRows);
                                    return newRows;
                                });

                                setTotalCount(totalCount);

                                resolve({
                                    data: upsellItemsList,
                                    page: page,
                                    totalCount: totalCount
                                })
                            })
                            .catch( error => {
                                console.log(`${fetchUpsellItemTypesAPI} Error`, error);
                                reject(error);
                            });
                        });
                    }
                } 
                actions={
                    [
                        {
                            icon: "create",
                            tooltip: "Edit Upsell Item",
                            onClick: (event, rowData) => {
                                console.log("Edit onClick", event, rowData);
                                history.push(`/adminpanel/upsellitemtype/edit/${rowData._id}`);
                            }
                        },
                        {
                            icon: "add_circle",
                            tooltip: "Add Upsell Item",
                            isFreeAction: true,
                            onClick: (event, rowData) => {
                                console.log("Add onClick", event, rowData);
                                history.push("/adminpanel/upsellitemtype/add");
                            }
                        }
                    ]
                } 
                options={
                    {
                        actionsColumnIndex: -1
                    }
                }
            />
        </div>
    )
}