import React, { useEffect, useState } from 'react';
import MaterialTable from 'material-table';

import {
    useHistory,
    useLocation,
    useParams
} from "react-router-dom";

import { lighten, makeStyles } from '@material-ui/core/styles';

import {
    Button,
    TextField,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle
} from '@material-ui/core';

import { authHeader } from '../../_helpers';

import { common } from '../../_constants/common';

import '../../App.css';

const useStyles = makeStyles((theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	visuallyHidden: {
		border: 0,
		clip: 'rect(0 0 0 0)',
		height: 1,
		margin: -1,
		overflow: 'hidden',
		padding: 0,
		position: 'absolute',
		top: 20,
		width: 1,
	},
}));


const apiUrl = 'http://localhost:5000';
const fetchLicenseeAPI = `${apiUrl}/admin/licensees`;
const saveProofOfIncorporationApproval = `${apiUrl}/admin/licensee/verify/proofOfIncorporation`;
const generateOTPAPI = `${apiUrl}/admin/licensee/otp/resend`;
const verifyOTPAPI = `${apiUrl}/admin/licensee/otp/verify`;
const resendInviteAPI = `${apiUrl}/admin/licensee/email/verify/resend`;
const forgotPasswordAPI = `${apiUrl}/admin/licensee/employee/password/forgot`;
const resetPasswordAPI = `${apiUrl}/admin/licensee/employee/password/reset`;


function TableActionButton(props) {
    console.log("TableActionButton", props);
    return (
        <span>{props.title}</span>
    );
}

export default function LicenseeList(props) {
    const classes = useStyles();

    let userData = localStorage.getItem('user');
    userData = JSON.parse(userData);
    console.log("userData", userData);

    let history = useHistory();
    console.log("history", history);

    let location = useLocation();
    console.log("location", location);

    let { from } = location.state || { from: { pathname: "/adminpanel/" } };
    if(!userData.acl.LICENSEE.includes("VIEW")) {
        history.replace(from);
    }

    const [rows, setRows] = React.useState([]);
    const [totalCount, setTotalCount] = React.useState(0);
    
    //-------------------------------------------------------------------------
    
    const otpVerifyDetailsStart = {
        licenseeId: "",
        mobile: "",
        country: common.country
    };

    const [dialogState, setDialogState] = useState(false);
    const [otp, setOTP] = useState("");
    const [otpVerifySucess, setOTPVerifySucess] = useState("");
    const [otpVerifyDetails, setOTPVerifyDetails] = useState(otpVerifyDetailsStart);

    function handleClickOpen() {
        setDialogState(true);
    }
    
    function handleClose() {
        setDialogState(false);
    }

    function handleVerifyOTP() {
        const requestOptions = {
            method: 'POST',
            headers: { 
                ...authHeader(),
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ 
                licenseeId: otpVerifyDetails.licenseeId, 
                mobile: otpVerifyDetails.mobile, 
                // mobile: "919664815262",
                country: otpVerifyDetails.country,
                otp: otp 
            })
        };
        console.log("requestOptions", requestOptions);
    
        fetch(verifyOTPAPI, requestOptions)
        .then( res => res.json() )
        .then( res => {
            console.log(`${verifyOTPAPI} Response`, res);
            // rowData.driverLicense.verified = true;
            // rowData.driverLicense.accepted = true;
            if(tableRef.current) {
                tableRef.current.onQueryChange();
            }

            setOTPVerifySucess("OTP Verified Successfully")
            setOTPVerifyDetails(otpVerifyDetailsStart);
            setTimeout(() => {
                handleClose();
            }, 5000);
            
        })
        .catch( error => {
            console.log(`${verifyOTPAPI} Error`, error);
        });
    }

    //-------------------------------------------------------------------------

    const tableRef = React.createRef();

    return (
        <div className={`t2y-list t2y-licenseelist ${classes.root}`}>

        <Dialog open={dialogState} 
                onClose={handleClose} 
                aria-labelledby="form-dialog-title"
                disableBackdropClick={true}
                disableEscapeKeyDown={true}>
            <DialogTitle id="form-dialog-title">OTP Verification</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Please Enter OTP
                </DialogContentText>
                { 
                    (otpVerifySucess !== "") &&
                    <DialogContentText>{otpVerifySucess}</DialogContentText>
                }
                <TextField
                    autoFocus
                    fullWidth
                    margin="dense"
                    id="name"
                    label="OTP"
                    type="otp"
                    value={otp}
                    onChange={ (e) => { 
                        setOTP( e.target.value );
                    } }
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="primary">
                    Cancel
                </Button>
                <Button onClick={handleVerifyOTP} color="primary">
                    Verify
                </Button>
            </DialogActions>
        </Dialog>

            <MaterialTable 
                title="Licensee"
                tableRef={tableRef}
                columns={[
                    { 
                        title: 'ID', 
                        field: 'licenseeRef', 
                        render: rowData => {
                            return rowData.licenseeRef
                        }
                    },
                    { 
                        title: 'Name', 
                        field: 'name', 
                        render: rowData => {
                            return rowData.name
                        } 
                    },
                    { 
                        title: 'Email', 
                        field: 'email', 
                        render: rowData => {
                            return rowData.email
                        } 
                    },
                    { 
                        title: 'Email Verified', 
                        field: 'isEmailVerified', 
                        render: rowData => {
                            return rowData.isEmailVerified ? "Yes": "No"
                        } 
                    },
                    { 
                        title: 'Mobile', 
                        field: 'mobile', 
                        render: rowData => {
                            return rowData.mobile
                        } 
                    },
                    { 
                        title: 'Mobile Verified', 
                        field: 'isMobileVerified', 
                        render: rowData => {
                            return rowData.isMobileVerified ? "Yes": "No"
                        } 
                    },
                    { 
                        title: 'Trailers', 
                        field: 'trailers', 
                        render: rowData => {
                            return (
                                <div>
                                    <a target="_blank" rel="noopener noreferrer" href={`/licensee/${rowData._id}/trailers`}>View Trailers</a>
                                </div>
                            );
                        } 
                    },
                    { 
                        title: 'Upsell Items', 
                        field: 'upsellItems', 
                        render: rowData => {
                            return (
                                <div>
                                    <a target="_blank" rel="noopener noreferrer" href={`/licensee/${rowData._id}/upsellitems`}>View Upsell Items</a>
                                </div>
                            );
                        } 
                    },
                    { 
                        title: 'Proof of Incorporation', 
                        field: 'proofOfIncorporation', 
                        render: rowData => {
                            return (
                                <div>
                                    <a target="_blank" rel="noopener noreferrer" href={rowData.proofOfIncorporation}>Download</a>
                                </div>
                            );
                        } 
                    },
                    { 
                        title: 'Proof of Incorporation Verified', 
                        field: 'proofOfIncorporationVerified', 
                        type: 'boolean',
                        render: rowData => {
                            return rowData.verified ? "Yes" : "No";
                        } 
                    },
                    { 
                        title: 'Proof of Incorporation Accepted', 
                        field: 'proofOfIncorporationAccepted', 
                        render: rowData => {
                            return rowData.accepted ? "Yes" : "No";
                        } 
                    }
                ]}
                data={ 
                    query => {
                        console.log("MaterialTable data", query);
                        const rowsPerPage = query.pageSize;
                        const page = query.page;

                        const requestOptions = {
                            method: 'GET',
                            headers: authHeader()
                        };
                        // return fetch(`${fetchLicenseeAPI}?count=${rowsPerPage}&skip${(page * rowsPerPage)}`, requestOptions)
                        return new Promise((resolve, reject) => {
                            // fetch(`${fetchLicenseeAPI}`, requestOptions)
                            fetch(`${fetchLicenseeAPI}?count=${rowsPerPage}&skip=${(page * rowsPerPage)}`, requestOptions)
                            .then( res => res.json() )
                            .then( res => {
                                console.log(`${fetchLicenseeAPI} Response`, res);
                                const licenseeList = res.licenseeList || [];
                                const totalCount = res.totalCount || 0;
                                
                                let newRows = [];
                                setRows((currentRows) => {
                                    console.log((typeof currentRows), (typeof licenseeList));
                                    newRows = [
                                        ...currentRows,
                                        ...licenseeList
                                    ];
                                    console.log("newRows", newRows);
                                    return newRows;
                                });

                                setTotalCount(totalCount);

                                resolve({
                                    data: licenseeList,
                                    page: page,
                                    totalCount: totalCount
                                })
                            })
                            .catch( error => {
                                console.log(`${fetchLicenseeAPI} Error`, error);
                                reject(error);
                            });
                        });
                    }
                }
                actions={
                    [
                        rowData => ({
                            icon: () => { return (<TableActionButton title="Approve Proof of Incorporation" />) },
                            tooltip: "Approve Proof of Incorporation",
                            disabled: (rowData.proofOfIncorporation ? false : true),
                            onClick: (event, rowData) => {
                                console.log("Approve onClick", event, rowData);
                                const requestOptions = {
                                    method: 'PUT',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ licenseeId: rowData._id, isAccepted: true })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(saveProofOfIncorporationApproval, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${saveProofOfIncorporationApproval} Response`, res);
                                    // rowData.driverLicense.verified = true;
                                    // rowData.driverLicense.accepted = true;
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }
                                })
                                .catch( error => {
                                    console.log(`${saveProofOfIncorporationApproval} Error`, error);
                                });
                            }
                        }),
                        rowData => ({
                            icon: () => { return (<TableActionButton title="Reject Proof of Incorporation" />) },
                            tooltip: "Reject Proof of Incorporation",
                            disabled: (rowData.proofOfIncorporation ? false : true),
                            onClick: (event, rowData) => {
                                console.log("Reject onClick", event, rowData);
                                const requestOptions = {
                                    method: 'PUT',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ licenseeId: rowData._id, isAccepted: false })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(saveProofOfIncorporationApproval, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${saveProofOfIncorporationApproval} Response`, res);
                                    // rowData.driverLicense.verified = true;
                                    // rowData.driverLicense.accepted = false;
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }
                                })
                                .catch( error => {
                                    console.log(`${saveProofOfIncorporationApproval} Error`, error);
                                });
                            }
                        }),
                        rowData => ({
                            icon: () => { return (<TableActionButton title="Resend OTP" />) },
                            tooltip: "Resend OTP",
                            onClick: (event, rowData) => {
                                console.log("Resend OTP onClick", event, rowData);
                                const requestOptions = {
                                    method: 'POST',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ 
                                        licenseeId: rowData._id,
                                        mobile: rowData.mobile,
                                        country: rowData.address.country || common.country 
                                    })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(generateOTPAPI, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${generateOTPAPI} Response`, res);
                                    // rowData.driverLicense.verified = true;
                                    // rowData.driverLicense.accepted = true;
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }

                                    setOTPVerifyDetails({ 
                                        customerId: rowData._id,
                                        mobile: rowData.mobile,
                                        country: rowData.address.country || common.country 
                                    });
                                    handleClickOpen();
                                })
                                .catch( error => {
                                    console.log(`${generateOTPAPI} Error`, error);
                                });
                            }
                        }),
                        rowData => ({
                            icon: () => { return (<TableActionButton title="Verify Email" />) } ,
                            tooltip: "Verify Email",
                            onClick: (event, rowData) => {
                                console.log("Verify Email", event, rowData);
                                const requestOptions = {
                                    method: 'POST',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ 
                                        licenseeId: rowData._id,
                                        email: rowData.email
                                    })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(resendInviteAPI, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${resendInviteAPI} Response`, res);
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }
                                })
                                .catch( error => {
                                    console.log(`${resendInviteAPI} Error`, error);
                                });
                            }
                        }),
                        {
                            icon: "create",
                            tooltip: "Edit Licensee",
                            onClick: (event, rowData) => {
                                console.log("Edit onClick", event, rowData);
                                history.push(`/adminpanel/licensee/edit/${rowData._id}`);
                            }
                        },
                        {
                            icon: "add_circle",
                            tooltip: "Add Licensee",
                            isFreeAction: true,
                            onClick: (event, rowData) => {
                                console.log("Licensee Signup onClick", event, rowData);
                                history.push("/adminpanel/licensee/signup");
                            }
                        }
                    ]
                }
                options={
                    {
                        actionsColumnIndex: -1
                    }
                }
            />
        </div>
    )
}