import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import MaterialTable from 'material-table';

import {
    Link
} from 'react-router-dom';

import { lighten, makeStyles } from '@material-ui/core/styles';

import { authHeader } from '../../_helpers';

import '../../App.css';

const apiUrl = 'http://localhost:5000';
const fetchServicingAPI = `${apiUrl}/admin/licensee/servicing`;
const saveServicingApproval = `${apiUrl}/admin/licensee/verify/servicing`;

const useStyles = makeStyles((theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	visuallyHidden: {
		border: 0,
		clip: 'rect(0 0 0 0)',
		height: 1,
		margin: -1,
		overflow: 'hidden',
		padding: 0,
		position: 'absolute',
		top: 20,
		width: 1,
	},
}));

function TrailerServicing(props) {
    const classes = useStyles();

    const [rows, setRows] = React.useState([]);
    const [totalCount, setTotalCount] = React.useState(0);
    
    const tableRef = React.createRef();

    return (
        <div className={classes.root}>
            <MaterialTable 
                title="Trailer Servicing"
                tableRef={tableRef}
                columns={[
                    { 
                        title: 'Licensee Name', 
                        field: 'licenseeName', 
                        render: rowData => {
                            console.log("rowData", rowData);
                            return rowData.licenseeName
                        } 
                    },
                    { 
                        title: 'Trailer Name', 
                        field: 'itemName', 
                        render: rowData => {
                            return rowData.itemName
                        } 
                    },
                    { 
                        title: 'Servicing Document', 
                        field: 'servicingDocument', 
                        render: rowData => {
                            // return <Link target="_blank" to={rowData.document}>Download</Link>
                            return (<a target="_blank" rel="noopener noreferrer" href={rowData.document}>Download</a>);
                        } 
                    },
                    { 
                        title: 'Servicing Document Verified', 
                        field: 'servicingDocumentVerified', 
                        type: 'boolean',
                        render: rowData => {
                            return rowData.verified ? "Yes" : "No";
                        } 
                    },
                    { 
                        title: 'Servicing Document Accepted', 
                        field: 'servicingDocumentAccepted', 
                        render: rowData => {
                            return rowData.accepted ? "Yes" : "No";
                        } 
                    }
                ]}
                data={ 
                    query => {
                        console.log("MaterialTable data", query);
                        const rowsPerPage = query.pageSize;
                        const page = query.page;

                        const requestOptions = {
                            method: 'GET',
                            headers: authHeader()
                        };
                        // return fetch(`${fetchServicingAPI}?count=${rowsPerPage}&skip${(page * rowsPerPage)}`, requestOptions)
                        return new Promise((resolve, reject) => {
                            // fetch(`${fetchServicingAPI}`, requestOptions)
                            fetch(`${fetchServicingAPI}?count=${rowsPerPage}&skip=${(page * rowsPerPage)}`, requestOptions)
                            .then( res => res.json() )
                            .then( res => {
                                console.log(`${fetchServicingAPI} Response`, res);
                                const servicingList = res.servicingList || [];
                                const totalCount = res.totalCount || 0;
                                
                                let newRows = [];
                                setRows((currentRows) => {
                                    console.log((typeof currentRows), (typeof servicingList));
                                    newRows = [
                                        ...currentRows,
                                        ...servicingList
                                    ];
                                    console.log("newRows", newRows);
                                    return newRows;
                                });

                                setTotalCount(totalCount);

                                resolve({
                                    data: servicingList,
                                    page: page,
                                    totalCount: totalCount
                                })
                            })
                            .catch( error => {
                                console.log(`${fetchServicingAPI} Error`, error);
                                reject(error);
                            });
                        });
                    }
                }
                actions={
                    [
                        {
                            icon: "check_circle",
                            tooltip: "Approve",
                            onClick: (event, rowData) => {
                                console.log("Approve onClick", event, rowData);
                                const requestOptions = {
                                    method: 'PUT',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ servicingId: rowData._id, isAccepted: true })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(saveServicingApproval, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${saveServicingApproval} Response`, res);
                                    // rowData.driverLicense.verified = true;
                                    // rowData.driverLicense.accepted = true;
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }
                                })
                                .catch( error => {
                                    console.log(`${saveServicingApproval} Error`, error);
                                });
                            }
                        },
                        {
                            icon: "cancel",
                            tooltip: "Reject",
                            onClick: (event, rowData) => {
                                console.log("Reject onClick", event, rowData);
                                const requestOptions = {
                                    method: 'PUT',
                                    headers: { 
                                        ...authHeader(),
                                        'Content-Type': 'application/json'
                                    },
                                    body: JSON.stringify({ servicingId: rowData._id, isAccepted: false })
                                };
                                console.log("requestOptions", requestOptions);
                            
                                fetch(saveServicingApproval, requestOptions)
                                .then( res => res.json() )
                                .then( res => {
                                    console.log(`${saveServicingApproval} Response`, res);
                                    // rowData.driverLicense.verified = true;
                                    // rowData.driverLicense.accepted = false;
                                    if(tableRef.current) {
                                        tableRef.current.onQueryChange();
                                    }
                                })
                                .catch( error => {
                                    console.log(`${saveServicingApproval} Error`, error);
                                });
                            }
                        }
                    ]
                }
                options={
                    {
                        actionsColumnIndex: -1
                    }
                }
            />
        </div>
    )
}

// cancel, check-circle

export default TrailerServicing;