import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import MaterialTable from 'material-table';
import {
    useHistory,
    useLocation,
    useParams
} from "react-router-dom";

import {
    Avatar,
    Button,
    CssBaseline,
    TextField,
    FormGroup,
    FormControlLabel,
    Checkbox,
    Link,
    Grid,
    Box,
    LockOutlinedIcon,
    Typography,
    Container,
    TextareaAutosize,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper
} from '@material-ui/core';

import { lighten, makeStyles } from '@material-ui/core/styles';

import { authHeader } from '../../_helpers';

import '../../App.css';

const apiUrl = 'http://localhost:5000';
const fetchTrailerTypesAPI = `${apiUrl}/admin/trailers`;

const useStyles = makeStyles((theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	visuallyHidden: {
		border: 0,
		clip: 'rect(0 0 0 0)',
		height: 1,
		margin: -1,
		overflow: 'hidden',
		padding: 0,
		position: 'absolute',
		top: 20,
		width: 1,
	},
}));

function TrailerList(props) {
    const classes = useStyles();

    let userData = localStorage.getItem('user');
    userData = JSON.parse(userData);
    console.log("userData", userData);

    let history = useHistory();
    console.log("history", history);

    let location = useLocation();
    console.log("location", location);

    let { from } = location.state || { from: { pathname: "/adminpanel/" } };
    if(!userData.acl.TRAILER.includes("VIEW")) {
        history.replace(from);
    }

    const [rows, setRows] = React.useState([]);
    const [totalCount, setTotalCount] = React.useState(0);
    
    const tableRef = React.createRef();

    return (
        <div className={classes.root}>
            <MaterialTable 
                title="Trailer List"
                tableRef={tableRef}
                search={false}
                columns={[
                    { 
                        title: 'ID', 
                        field: 'trailerTypeRef', 
                        render: rowData => {
                            return rowData.trailerTypeRef
                        }
                    },
                    { 
                        title: 'Name', 
                        field: 'name', 
                        render: rowData => {
                            return rowData.name
                        },
                        cellStyle: { verticalAlign: 'top' }
                    },
                    { 
                        title: 'Type', 
                        field: 'type', 
                        render: rowData => {
                            return rowData.type
                        },
                        cellStyle: { verticalAlign: 'top' }
                    },
                    { 
                        title: 'Description', 
                        field: 'description', 
                        render: rowData => {
                            return (
                                <div style={{
                                    /* height: "100px", 
                                    whiteSpace: "nowrap",
                                    overflow: "hidden",
                                    textOverflow: "ellipsis"  */
                                }}>{rowData.description}</div>
                            )
                        },
                        cellStyle: { 
                            verticalAlign: 'top', 
                            
                        }
                    },
                    { 
                        title: 'Size', 
                        field: 'size', 
                        render: rowData => {
                            return rowData.size
                        },
                        cellStyle: { verticalAlign: 'top' }
                    },
                    { 
                        title: 'Capacity', 
                        field: 'capacity', 
                        render: rowData => {
                            return rowData.capacity
                        },
                        cellStyle: { verticalAlign: 'top' }
                    },
                    { 
                        title: 'Tare', 
                        field: 'tare', 
                        render: rowData => {
                            return rowData.tare
                        },
                        cellStyle: { verticalAlign: 'top' }
                    },
                    { 
                        title: 'Features', 
                        field: 'features', 
                        render: rowData => {
                            return rowData.features
                        },
                        cellStyle: { verticalAlign: 'top' }
                    },
                    { 
                        title: 'Photos', 
                        field: 'photos', 
                        render: rowData => {
                            return (
                                <div>
                                    {
                                        rowData.photos.map((photo, photoIndex) => {
                                            return (
                                                <div>
                                                    <a key={rowData._id+"_photo"+photoIndex} href={photo}>Photo {(photoIndex + 1)}</a>
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                            );
                        },
                        cellStyle: { verticalAlign: 'top' }
                    },
                    { 
                        title: 'Is Available?', 
                        field: 'availability', 
                        render: rowData => {
                            return rowData.availability ? "Yes" : "No"
                        },
                        cellStyle: { verticalAlign: 'top' }
                    },
                    { 
                        title: 'is Featured?', 
                        field: 'isFeatured', 
                        render: rowData => {
                            return rowData.isFeatured ? "Yes" : "No"
                        },
                        cellStyle: { verticalAlign: 'top', height: "auto" }
                    }
                ]}
                data={ 
                    query => {
                        console.log("MaterialTable data", query);
                        const rowsPerPage = query.pageSize;
                        const page = query.page;

                        const requestOptions = {
                            method: 'GET',
                            headers: authHeader()
                        };
                        // return fetch(`${fetchTrailerTypesAPI}?count=${rowsPerPage}&skip${(page * rowsPerPage)}`, requestOptions)
                        return new Promise((resolve, reject) => {
                            // fetch(`${fetchTrailerTypesAPI}`, requestOptions)
                            fetch(`${fetchTrailerTypesAPI}?count=${rowsPerPage}&skip=${(page * rowsPerPage)}`, requestOptions)
                            .then( res => res.json() )
                            .then( res => {
                                console.log(`${fetchTrailerTypesAPI} Response`, res);
                                const trailersList = res.trailersList || [];
                                const totalCount = res.totalCount ? res.totalCount : 0;
                                
                                let newRows = [];
                                setRows((currentRows) => {
                                    console.log((typeof currentRows), (typeof trailersList));
                                    newRows = [
                                        ...currentRows,
                                        ...trailersList
                                    ];
                                    console.log("newRows", newRows);
                                    return newRows;
                                });

                                setTotalCount(totalCount);

                                resolve({
                                    data: trailersList,
                                    page: page,
                                    totalCount: totalCount
                                })
                            })
                            .catch( error => {
                                console.log(`${fetchTrailerTypesAPI} Error`, error);
                                reject(error);
                            });
                        });
                    }
                }
                actions={
                    [
                        {
                            icon: "create",
                            tooltip: "Edit Trailer",
                            onClick: (event, rowData) => {
                                console.log("Edit onClick", event, rowData);
                                history.push(`/adminpanel/trailertype/edit/${rowData._id}`);
                            },
                            cellStyle: { verticalAlign: 'top' }
                        },
                        {
                            icon: "add_circle",
                            tooltip: "Add Trailer",
                            isFreeAction: true,
                            onClick: (event, rowData) => {
                                console.log("Add onClick", event, rowData);
                                history.push("/adminpanel/trailertype/add");
                            },
                            cellStyle: { verticalAlign: 'top' }
                        }
                    ]
                }
                options={
                    {
                        actionsColumnIndex: -1,
                        rowStyle: {
                            height: "100px",
                            maxHeight: "100px",
                            overflow: "scroll"
                        }
                    }
                }
            />
        </div>
    )
}

// cancel, check-circle

export default TrailerList;