import React, { useState } from 'react';
import {
    Avatar,
    Button,
    CssBaseline,
    TextField,
    FormControlLabel,
    Checkbox,
    Link,
    Grid,
    Box,
    LockOutlinedIcon,
    Typography,
    Container
} from '@material-ui/core';

import { 
    makeStyles 
} from '@material-ui/core/styles';

import {
    useLocation,
    useHistory,
    useParams
} from "react-router-dom";

import { authHeader } from '../../_helpers';

const apiUrl = 'http://localhost:5000';
const signInUrl = `${apiUrl}/admin/employee/signin`;

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    }
}));



export default function EmployeeSignIn() {
    const classes = useStyles();

    let history = useHistory();
    console.log("history", history);

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [submitted, setSubmitted] = useState(false);

    const [successMessage, setSuccessMessage] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    /* async function handleSubmit(e) {
        e.preventDefault();

        setSubmitted(true);
    
        if (email && password) {
            await props.login(email, password);
            console.log("Logged In", history);
            // history.push("/adminpanel/");
        }
    } */

    function onSignInSubmit(e) {
        e.preventDefault();

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ email, password })
        };
        
        // console.log("config", config);

        fetch(signInUrl, requestOptions)
        .then( res => res.json() )
        .then(res => {
            console.log(res);
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            localStorage.setItem('user', JSON.stringify(res.employeeObj));
            localStorage.setItem('token', JSON.stringify(res.token));
            localStorage.setItem('expiry', new Date().getTime() + (8 * 60 * 60 * 1000));

            localStorage.setItem('aclSettingsAdmin', JSON.stringify(res.aclSettingsAdmin));
            localStorage.setItem('aclSettingsLicensee', JSON.stringify(res.aclSettingsLicensee));

            setErrorMessage("");
            setSuccessMessage("Successfully Signed in");

            history.push("/adminpanel/");
        })
        .catch( err => {
            console.log("SignIn Error", err);
            setErrorMessage("Invalid Credentials");
            setSuccessMessage("");
        });
    }

    return (
        <Container component="main" maxWidth="xs">

            <CssBaseline />

            <div className={classes.paper}>

                {/* <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar> */}

                <Typography component="h1" variant="h5">
                    Sign In
                </Typography>

                { 
                    (successMessage !== "") &&
                    <Typography component="h6" variant="h5">
                        {successMessage}
                    </Typography>
                }

                { 
                    (errorMessage !== "") &&
                    <Typography component="h6" variant="h5">
                        {errorMessage}
                    </Typography>
                }

                <form className={classes.form} noValidate>

                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="email"
                        label="Email Address"
                        name="email"
                        autoComplete="email"
                        autoFocus
                        value={email}
                        onChange={ (e) => { setEmail(e.target.value) } }
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        label="Password"
                        type="password"
                        id="password"
                        autoComplete="current-password"
                        value={password}
                        onChange={ (e) => { setPassword(e.target.value) } }
                    />

                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        onClick={onSignInSubmit}
                    >
                        Sign In
                    </Button>

                </form>

            </div>
        
        </Container>
    );
}