import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import MaterialTable from 'material-table';

import { lighten, makeStyles } from '@material-ui/core/styles';

import { authHeader } from '../../_helpers';

import '../../App.css';

const apiUrl = 'http://localhost:5000';
const fetchCustomerPaymentsAPI = `${apiUrl}/admin/customer/payments`;

const useStyles = makeStyles((theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	visuallyHidden: {
		border: 0,
		clip: 'rect(0 0 0 0)',
		height: 1,
		margin: -1,
		overflow: 'hidden',
		padding: 0,
		position: 'absolute',
		top: 20,
		width: 1,
	},
}));

function CustomerPayment(props) {
    const classes = useStyles();

    const [rows, setRows] = React.useState([]);
    const [totalCount, setTotalCount] = React.useState(0);
    
    const tableRef = React.createRef();

    return (
        <div className={classes.root}>
            <MaterialTable 
                title="Payments - Inward (From Customers to T2Y)"
                tableRef={tableRef}
                columns={[
                    { 
                        title: 'Customer Name', 
                        field: 'customerName', 
                        render: rowData => {
                            return rowData.customerName
                        } 
                    },
                    { 
                        title: 'Licensee Name', 
                        field: 'licenseeName', 
                        render: rowData => {
                            return rowData.licenseeName
                        } 
                    },
                    { 
                        title: 'Total Paid', 
                        field: 'totalPaid', 
                        render: rowData => {
                            return rowData.totalPaid ? rowData.totalPaid : 0
                        } 
                    },
                    { 
                        title: 'Authorization Transaction ID', 
                        field: 'transactionIdAuth', 
                        render: rowData => {
                            return rowData.transactionIdAuth ? rowData.transactionIdAuth : ""
                        } 
                    },
                    { 
                        title: 'Authorization Transaction Date', 
                        field: 'transactionAuthDate', 
                        render: rowData => {
                            return rowData.transactionAuthDate ? rowData.transactionAuthDate : ""
                        } 
                    },
                    { 
                        title: 'Transaction Auth Action', 
                        field: 'authTransactionAction', 
                        render: rowData => {
                            return rowData.authTransactionAction ? rowData.authTransactionAction : ""
                        } 
                    },
                    { 
                        title: 'Action Transaction ID', 
                        field: 'transactionIdAction', 
                        render: rowData => {
                            return rowData.transactionIdAction ? rowData.transactionIdAction : ""
                        } 
                    },
                    { 
                        title: 'Action Transaction Date', 
                        field: 'transactionActionDate', 
                        render: rowData => {
                            return rowData.transactionActionDate ? rowData.transactionActionDate : ""
                        } 
                    },
                ]}
                data={ 
                    query => {
                        console.log("MaterialTable data", query);
                        const rowsPerPage = query.pageSize;
                        const page = query.page;

                        const requestOptions = {
                            method: 'GET',
                            headers: authHeader()
                        };
                        // return fetch(`${fetchCustomerPaymentsAPI}?count=${rowsPerPage}&skip${(page * rowsPerPage)}`, requestOptions)
                        return new Promise((resolve, reject) => {
                            // fetch(`${fetchCustomerPaymentsAPI}`, requestOptions)
                            fetch(`${fetchCustomerPaymentsAPI}?count=${rowsPerPage}&skip=${(page * rowsPerPage)}`, requestOptions)
                            .then( res => res.json() )
                            .then( res => {
                                console.log(`${fetchCustomerPaymentsAPI} Response`, res);
                                const customerPaymentsList = res.customerPaymentsList || [];
                                const totalCount = res.totalCount || 0;
                                
                                let newRows = [];
                                setRows((currentRows) => {
                                    console.log((typeof currentRows), (typeof customerPaymentsList));
                                    newRows = [
                                        ...currentRows,
                                        ...customerPaymentsList
                                    ];
                                    console.log("newRows", newRows);
                                    return newRows;
                                });

                                setTotalCount(totalCount);

                                resolve({
                                    data: customerPaymentsList,
                                    page: page,
                                    totalCount: totalCount
                                })
                            })
                            .catch( error => {
                                console.log(`${fetchCustomerPaymentsAPI} Error`, error);
                                reject(error);
                            });
                        });
                    }
                }
            />
        </div>
    )
}

// cancel, check-circle

export default CustomerPayment;